<template>
    <div class="page">
        <times :time="time" :show="false"/>
        <div class="area">
            <el-form
                :inline="true"
                :model="formInline"
                class="demo-form-inline"
                label-width="80px"
                size="small"
                label-position="left"
            >
                <el-row>
                    <el-form-item label="区域">
                        <el-select v-model="formInline.region" placeholder="客户所在省份">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label>
                        <el-select v-model="formInline.region" placeholder="客户所在城市">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label>
                        <el-select v-model="formInline.region" placeholder="客户所在区县">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-row>
                <el-row>
                    <el-form-item label="业务类型">
                        <el-radio-group v-model="radio" size="small">
                            <el-radio-button label="备电"></el-radio-button>
                            <el-radio-button label="充电"></el-radio-button>
                            <el-radio-button label="换电"></el-radio-button>
                            <el-radio-button label="售电"></el-radio-button>
                            <el-radio-button label="储能站"></el-radio-button>
                            <el-radio-button label="延寿站"></el-radio-button>
                        </el-radio-group>
                    </el-form-item>
                </el-row>
                <el-row>
                    <el-form-item label="活动时间">
                        <el-date-picker
                            v-model="formInline.time"
                            type="datetimerange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                        ></el-date-picker>
                    </el-form-item>
                </el-row>
                <el-row></el-row>
                <el-row>
                    <el-form-item label="用户">
                        <el-input v-model="formInline.user" placeholder="请输入用户名称"></el-input>
                    </el-form-item>
                    <el-form-item label>
                        <el-select v-model="formInline.region" placeholder="客户类型">
                            <el-option label="金融机构" value="shanghai"></el-option>
                            <el-option label="医疗卫生" value="beijing"></el-option>
                            <el-option label="公安交警" value="shanghai"></el-option>
                            <el-option label="学校教育" value="beijing"></el-option>
                            <el-option label="单位机房" value="shanghai"></el-option>
                            <el-option label="数据中心" value="beijing"></el-option>
                            <el-option label="物业管理" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label>
                        <el-input v-model="formInline.user" placeholder="用户地址"></el-input>
                    </el-form-item>
                    <el-form-item label>
                        <el-input v-model="formInline.user" placeholder="用户"></el-input>
                    </el-form-item>
                </el-row>
                <el-row>
                    <el-button type="primary">查询</el-button>
                </el-row>
            </el-form>
        </div>
        <div class="area" style="margin-top: 20px;padding:20px;">
            <el-row>
                <el-button type="primary" @click="gotoadd">新建</el-button>
            </el-row>
            <el-table
                :data="tableData"
                style="width: 100%;margin-top:10px;"
                :header-cell-style="{background:'#E4EAEC',color:'#666',fontSize:'12px'}"
            >
                <el-table-column prop="date" label="日期" width="180"></el-table-column>
                <el-table-column prop="name" label="姓名" width="180"></el-table-column>
                <el-table-column prop="address" label="地址"></el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import times from "../../../components/tableTime";
import layouts from "../../../components/layouts";
import tablemap from "../../../components/table_map";
export default {
    data() {
        return {
            time: "2019-06-14 10:48:53",
            activeName: "chuneng",
            formInline: {
                user: "",
                region: ""
            },
            radio: "备电",
            tableData: []
        };
    },
    components: {
        times,
        layouts,
        tablemap
    },
    created() {},
    mounted() {},
    methods: {
        onSubmit() {
            console.log("submit!");
        },
        gotoadd(){
            this.$router.push({ path:'/customer/add'})
        }
    }
};
</script>

<style scoped lang='scss'>
.page {
    padding-top: 0.7%;
    width: 1280px;
    min-height: 700px;
    margin: 0 auto;
}
.area {
    background: #fff;
    padding: 20px 0 20px 25px;
}
.line {
    text-align: center;
}
</style>


<style scoped>
</style>



